import React from "react";
import {
  NativeSelect,
  OutlinedInput,
  FormControl,
  InputLabel
} from "@material-ui/core";
import { connect } from "react-redux";
import { mapStateToProps } from "../../store/mapStateToProps";
import { withStyles } from "@material-ui/core/styles";
const options = ["Africa", "Americas", "Asia", "Europe", "Oceania"];
const styles = theme => ({
  inputs: {
    backgroundColor: theme.palette.primary.main
  },
  inputsLight: {
    backgroundColor: theme.palette.primary.inputs
  },
  inputLabel: {
    color: theme.palette.primary.contrastText,
  },
   // Name of the component ⚛️ / style sheet
    root: { // Name of the rule
      color: "orange",
      "&:focused": { 
        color: "cyan"
      }
    
  },
  selectListDark: {
      width: 300,
    color: theme.palette.primary.contrastText,
    backgroundColor: theme.palette.primary.main
  },
  selectListLight: {
      width: 300,
    backgroundColor: theme.palette.primary.inputs,
    color: theme.palette.primary.contrastText
  }
});
const Form = props => {
  const { classes } = props;
  const { palette } = props.palette; 
  return (
    <form action={e => e.preventDefault()} style={{ marginTop: 150 }}>
      <FormControl>
        <OutlinedInput
          className={
            props.lightMode ? classes.selectListLight : classes.selectListDark
          }
        />
      </FormControl>
          {console.log(palette)}
      <FormControl className={classes.formControl} >
        <InputLabel style={{color: palette.primary.contrastText}}>Filter By Region</InputLabel>
        <NativeSelect
          className={
            props.lightMode ? classes.selectListLight : classes.selectListDark
          }
          name="Filter By Region"
        >
          {options.map(option => {
            return <option style={{backgroundColor: palette.primary.main, }}>{option}</option>;
          })}
        </NativeSelect>
      </FormControl>
    </form>
  );
};

export default withStyles(styles)(Form);
